.userWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-top: 45px;
  padding-right: 20px;
}

.user {
  width: 100%;
  display: grid;
  grid-template-columns: minmax(75px, 1fr) 7fr;
  background: rgba(200, 200, 200, 0.2);
  border-radius: 10px;
  grid-gap: 20px;
  margin-bottom: 20px;
}

.block__left {
  width: 100%;
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  width: 65px;
  border-radius: 35px;
  border: 2px solid #BADA55;
}

.name {
  padding: 5px 0;
  font-weight: bold;
}

.block__right {
  width: 100%;
  flex-direction: column;
  display: flex;
  padding: 5px;
}

.block__right span {
  margin-bottom: 5px;
}

.city {
}

.userPagintaiton span {
  padding: 0 5px;
  cursor: pointer;
}

.activePage {
  color: white;
}

.normalPage {
  color: #333;
}
